/* Modal.css */
.ModalContent {
  background-color: #b3b3b3;
  color: black;
  width: 80vw;
  height: 80vh;
  margin: auto;
  padding: 40px;
  position: relative;
  z-index: 1000000;
  border: none;
  align-content: center;
}
.ModalTitle,
.ModalText {
  color: black;
  font-size: 14px;
  line-height: 22px;
}
#ModalDialogue {
  width: 100%;
  font-size: 2em;
  color: black;
  border-bottom: 0px solid rgb(50, 50, 50);
}
.ModalOverlay {
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000000;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.ButtonWrap {
  position: absolute;
  width: 600px;
  height: 100px;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
  bottom: 0;
}

.CloseButton {
  padding: 10px;
  width: 200px;
  height: 80px;
  font-size: 20px;
  border: none;
  color: white;
  background-color: #5c5c5c;
  cursor: pointer;
}
.CloseButton:hover {
  background-color: red;
}

.PrintButton {
  padding: 10px;
  color: white;
  background-color: black;
  width: 200px;
  height: 80px;
  font-size: 20px;
  border: none;
  cursor: pointer;
  margin-right: 30px;
}
.PrintButton:hover {
  background-color: blue;
}

.VimeoAndComment {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 0px 30px;
  grid-template-areas: " vimeo comment";
}

.vimeoWrap {
  grid-area: vimeo;
  width: 100%;
  height: 100%;
  position: relative;
}

.vimeoVideo {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.commentWrap {
  grid-area: comment;
}

.ModalComment {
  color: black;
  font-size: 14px;
  line-height: 22px;
}
