.CharacterSection {
  grid-area: Character;
  width: 478px;
  height: 178px;
  border-radius: 12px;
  box-sizing: border-box;
  display: flex;
  overflow-x: hidden;
  overflow-y: hidden;
  margin: 14px 0px 0px 0px;
  padding: 0px 5px 0px 5px;
  border: 2px solid #353535;
}
.CharacterWrap {
  width: 456px;
  height: 156px;
  background-color: black;
  box-sizing: border-box;
  border-radius: 4px;
  display: flex;
  overflow-x: scroll;
  overflow-y: hidden;
  padding: 1px 2px;
  margin: 10px 2px 1px 4px;
}

.Character {
  background-color: #828282;
  border: 2px solid black;
  border-radius: 6px;
  position: relative;
  width: 32px;
}
.Character input {
  display: none;
}
.CharacterSection .subtitle {
  background-color: #b3b3b3;
  color: #353535;
  top: 110px;
  left: 12px;
  position: absolute;
  padding: 0 6px;
  font-size: 14px;
}
.Character .Param {
  font-size: 0.8em;
  line-height: 1.1em;
  width: 20px;
  display: block;
  padding: 3px 0px 2px;
  border-radius: 8px;
  text-align: center;
  background: #9b9b9b;
  color: black;
  top: 50px;
  left: 50%;
  font-size: 12px;
  transform: translateX(-50%);
  margin: 0px;
  position: relative;
}
.Character .Scope {
  font-size: 0.7em;
  font-weight: 800;
  padding: 0 4px;
  margin: 4px;
  text-align: center;
  background: rgb(20, 20, 20);
  color: rgb(114, 114, 114);
  border-radius: 2px;
}
.CharacterInput {
  display: none;
}
.CharacterLabel {
  display: block;
  width: 94px;
  height: 35px;
  cursor: pointer;
  border-radius: 8px;
  transition: 250ms;
  transform: rotate(-90deg);
  top: 18px;
  left: -25px;
  position: relative;
}

.CharacterLabel::after {
  position: absolute;
  content: "";
  top: -1px;
  left: 0px;
  transition: 250ms ease-in-out;
  background: linear-gradient(
    90deg,
    rgb(126, 126, 126) 0%,
    rgb(121, 121, 121) 10%,
    rgba(0, 0, 0, 1) 90%,
    rgb(162, 162, 162) 100%
  );
  height: 2em;
  width: 2.5em;
  border-left: 10px solid black;
  border-right: 8px solid black;
  height: 25px;
  width: 18px;
  border-left: 8px solid #000000;
  border-right: 8px solid #000000;
}
.Character .Param:hover {
  color: #ffee00;
}

.CharacterLabel::before {
  position: absolute;
  content: "";
  width: 70px;
  height: 6px;
  top: 9px;
  left: 10px;
  border-radius: 5px;
  transition: 250ms ease-in-out;
  background: black;
  box-shadow: 0 0.08em 0.15em -0.1em rgba(0, 0, 0, 0.5) inset,
    0 0.05em 0.08em -0.01em rgba(255, 255, 255, 0.7),
    0 0 0 0 rgb(113 113 113 / 70%) inset;
}

input:checked + .CharacterLabel::before {
  box-shadow: 0 0.08em 0.15em -0.1em rgba(0, 0, 0, 0.5) inset,
    0 0.05em 0.08em -0.01em rgba(255, 255, 255, 0.7), 5em 0 0 0 #ffee00 inset;
  animation: flicker 0.3s infinite 0.3s;
}

input:checked + .CharacterLabel::after {
  left: 2.9em;
}

.CharacterWrap::-webkit-scrollbar {
  height: 10px;
  box-sizing: border-box;
}

.CharacterWrap::-webkit-scrollbar-track {
}

.CharacterWrap::-webkit-scrollbar-thumb {
  background: rgb(0, 0, 0);
  border-radius: 2px;
  border-right: 5px solid rgb(85, 85, 85);
  border-left: 5px solid rgb(85, 85, 85);
  background-image: url(../../imgs/scroll_bg.png);
  background-size: contain;
}

.CharacterWrap::-webkit-scrollbar-thumb:hover {
  background: #999999;
}
