@font-face {
    /* 기본스타일  */
  font-family: 'ChungjuKimSaengTTF';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2312-1@1.1/ChungjuKimSaengTTF.woff2') format('woff2');
}
@font-face {
  /* 글자스타일 1 */
  font-family: "Hahmlet-Regular";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2110@1.0/Hahmlet-Regular.woff2")
    format("woff2");
}
@font-face {
  /* 글자스타일 2 */
  font-family: "국립박물관문화재단클래식B";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_twelve@1.0/국립박물관문화재단클래식B.woff")
    format("woff");
}
@font-face {
  /* 글자스타일 3 */
  font-family: "BMEULJIRO";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_twelve@1.0/BMEULJIRO.woff")
    format("woff");
}
@font-face {
  /* 글자스타일 4 */
  font-family: "SBAggroB";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2108@1.1/SBAggroB.woff")
    format("woff");
}
@font-face {
  /* 글자스타일 5 */
  font-family: "양진체";
  src: url("https://cdn.jsdelivr.net/gh/supernovice-lab/font@0.9/yangjin.woff")
    format("woff");
}
@font-face {
  /* 글자스타일 6 */
  font-family: "dimibang_new";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2104@1.1/dimibang_new.woff")
    format("woff");
}

.style-1 {
  font-family: "Hahmlet-Regular", serif;
  font-weight: 900;
}
.style-2 {
  font-family: "국립박물관문화재단클래식B", serif;
  font-weight: 700;
}
.style-3 {
  font-family: "BMEULJIRO", serif;
}
.style-4 {
  font-family: "SBAggroB", sans-serif;
  font-weight: 500;
}
.style-5 {
  font-family: "양진체", sans-serif;
}
.style-6 {
  font-family: "dimibang_new", serif;
}

@media print {
  body * {
    visibility: hidden;
  }

  #printable-modal-content,
  #printable-modal-content * {
    visibility: visible;
  }

  #printable-modal-content {
    position: absolute;
    left: 0;
    top: 0;
  }
}
